footer div h1 {
    text-align: center;
    color: white;

}

a.contact-footer {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

@media only screen and (max-width: 590px) {
    footer {
        display: flex;
        flex-direction: column;
        background-color: #003057;
        padding: 20px;
    }

    footer>* {
        color: white;
        justify-content: center;
        margin: auto;
        align-items: center;
    }

    footer div h1 {
        text-align: center;
        color: white;
    
    }

    footer div p {
        text-align: left;
        font-style: italic;
        color: white;
    }

    footer #links {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    #left-column,
    #right-column {
        width: 100%;
    }


    #right-column div .contact {
        text-transform: lowercase;
    }

    #center-column {
        width: 100%;
        border-top: 1px solid #fffff7;
        border-bottom: 1px solid #fffff7;
    }

    .logo {
        margin-right: 10px;
    }

    #credits {
        text-align: center;
        background-color: #003057;
        color: white;
        display: block;
        margin: 0;
        padding: 20px;
    }

    #center-column ul {
        font-style: normal;
        font-weight: 400;
        font-size: 22px;
        line-height: 111%;
        list-style-type: none;
        padding: 0;
    }

    #center-column ul li {
        text-align: center;
        color: white;
    }

    .text-design{
        text-align: center;
    }
    .text-design{
        display: flex;
        justify-content: center;
        align-items: center;
    }

    img.socials{
        margin: 15px;
    }

    a.contact-footer{
        color: white;
    }
}

